import { getYearWeek } from './util';
import dateFormat from 'date-fns/format';
export var TIME_BLOCK = {
  lineHeight: 22,
  padding: 8
};
export var DAY_MILLISECONDS = 86400000;
export var RANGE_SPLIT = '~';
export var FORMATS = {
  date: 'YYYY-MM-DD',
  month: 'YYYY-MM',
  year: 'YYYY',
  time: 'HH:mm:ss',
  daterange: 'YYYY-MM-DD',
  week: 'YYYY',
  weekrange: 'YYYY'
};
export var isVaildDate = function isVaildDate(date) {
  return date && (date instanceof Date || date.startDate || typeof date === 'number');
};
export var formatterDate = function formatterDate(type, date, formatter, showTime, localeDatas) {
  if (!isVaildDate(date)) {
    return '';
  }

  date = new Date(date);
  var str = '';

  switch (type) {
    // case 'daterange':
    //   if (date instanceof Date) { date = {startDate: date, endDate: date} }
    //   str = dateFormat(date.startDate, `${formatter}${showTime ? ' hh:mm:ss' : ''}`) + RANGE_SPLIT + dateFormat(date.endDate, `${formatter}${showTime ? ' hh:mm:ss' : ''}`)
    //   break
    case 'weekrange':
      // if (date instanceof Date) { date = {startDate: date, endDate: date} }
      str = localeDatas.datePicker.weekrange(date.getFullYear(), getYearWeek(date));
      break;

    case 'year':
    case 'month':
    case 'time':
      str = dateFormat(date, formatter);
      break;

    case 'week':
      str = localeDatas.datePicker.weekrange(date.getFullYear(), getYearWeek(date));
      break;

    default:
      str = dateFormat(date, '' + formatter + (showTime ? ' HH:mm:ss' : ''));
      break;
  }

  return str;
};